import React from 'react';
import { graphql, navigate } from 'gatsby';
import { Pagination } from 'antd';
import Img from 'gatsby-image';

// Components
import Layout from '../Layout/Layout';
import BlogsContainer from '../BlogsContainer/BlogsContainer';
import EachBlogComp from '../EachBlogComp/EachBlogComp';
import EachBlogP from '../EachBlogP/EachBlogP';
import EachBlogDescDate from '../EachBlogDescDate/EachBlogDescDate';

export const query = graphql`
	query($limit: Int!, $skip: Int!) {
		allContentfulBlog(skip: $skip, limit: $limit) {
			edges {
				node {
					title {
						content {
							content {
								value
							}
						}
					}
					desc {
						content {
							content {
								value
							}
						}
					}
					date {
						content {
							content {
								value
							}
						}
					}
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
				}
			}
		}
	}
`;

function Blogs(props) {
	const pageNum = props.pageContext.numberOfPages * props.pageContext.limit;
	const handlePage = (num) => {
		if (num === 1) {
			navigate(`/blog`);
		} else {
			navigate(`/blog/${num}`);
		}
	};
	return (
		<Layout title={`همه ی مطالب`}>
			<BlogsContainer>
				{props.data.allContentfulBlog.edges.map((each) => {
					const title = each.node.title.content[0].content[0].value;
					return (
						<EachBlogComp key={title} url={`/blog/${title}`}>
							<EachBlogP>{title}</EachBlogP>
							<div dir="rtl" className="d-flex">
								<Img style={{ borderRadius: '5px' }} alt="img" fluid={each.node.img.fluid} />
								<EachBlogDescDate
									desc={each.node.desc.content[0].content[0].value}
									date={each.node.date.content[0].content[0].value}
								/>
							</div>
						</EachBlogComp>
					);
				})}
			</BlogsContainer>
			<div className="container d-flex justify-content-center mt-4">
				<Pagination
					current={props.pageContext.humanPageNumber}
					pageSize={6}
					total={pageNum}
					onChange={handlePage}
				/>
			</div>
		</Layout>
	);
}

export default Blogs;
